.Cart {
  /* position: ; */
  top: 0;
  /* right: -450px; */
  /* width: 450px; */
  height: 100%;
  background-color: #1b1a20;
  box-sizing: border-box;
  -webkit-transition: right 0.2s;
  transition: right 0.2s;
  z-index: 999999;
  /* display: grid;
  grid-template-columns: repeat(4, 1fr); */
}

.Cart-open {
  right: 0;
}

.Bag {
  position: absolute;
  margin-right: 15px;
  background-image: url(data:image/png;base64,);
  background-repeat: no-repeat;
  background-position: 50%;
  left: 70%;
  bottom: 30%;
  width: 60px;
  height: 60px;
  background-color: #000;
  background-size: 50%;
  cursor: pointer;
}

.Quantity {
  width: 18px;
  height: 18px;
  color: #0c0b10;
  font-weight: 700;
  font-size: 0.7em;
  text-align: center;
  line-height: 18px;
  border-radius: 50%;
  background-color: #eabf00;
  position: absolute;
  bottom: 5px;
  right: 10px;
}

.Container {
  max-height: 540px;
  /* overflow-y: auto; */
  /* display: block; */
  max-width: 1168px;
  margin: 0 auto;
}

.Footer {
  /* display: none; */
  box-sizing: border-box;
  padding: 5%;
  /* position: absolute; */
  bottom: 0;
  width: 100%;
  height: 200px;
  z-index: 2;
  background-color: #1b1a20;
}

.Footer:before {
  content: "";
  width: 100%;
  height: 20px;
  display: block;
  position: absolute;
  top: -20px;
  left: 0;
  background: -webkit-linear-gradient(bottom, rgba(0, 0, 0, 0.2), transparent);
  background: linear-gradient(0deg, rgba(0, 0, 0, 0.2), transparent);
}

.Sub {
  width: 20%;
  color: #5b5a5e;
}

.Total-amount {
  width: 80%;
  text-align: right;
  color: #eabf00;
  font-size: 22px;
}

.Buy-btn {
  color: #ececec;
  text-transform: uppercase;
  background-color: #0c0b10;
  text-align: center;
  padding: 15px 0;
  margin-top: 20px;
  cursor: pointer;
  -webkit-transition: background-color 0.2s;
  transition: background-color 0.2s;
}

.Buy-btn:hover {
  background-color: #eabf00;
}

.Total {
  display: grid;
  grid-template-rows: auto;
  grid-template-columns: repeat(2, 1fr);
  align-items: center;
}

.Content {
  padding-top: 50px;

  /* display: none; */
}
